@font-face {
  font-family: 'DINProDev';
  src: local("DIN Pro Light"), local("DINPro-Light"), url("../fonts/DinPro/DINPro-Light.woff") format("woff"), url("../fonts/DinPro/DinPro-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'DINProDev';
  src: local("DIN Pro"), local("DINPro"), url("../fonts/DinPro/DINPro.woff") format("woff"), url("../fonts/DinPro/DinPro.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'DINProDev';
  src: local("DIN Pro Medium"), local("DINPro-Medium"), url("../fonts/DinPro/DINPro-Medium.woff") format("woff"), url("../fonts/DinPro/DinPro-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a,
button,
input,
input::placeholder,
textarea,
textarea::placeholder {
  font-family: "DINProDev", "sans serif";
}

body {
  margin: 0;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "DINProDev", "sans serif";
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:hover,
button:hover,
input:hover {
  cursor: pointer;
}

.tab-form__container {
  max-width: 1170px;
  margin: 0 auto;
  margin-bottom: 126px;
}

.tab-form__main-title {
  font-size: 40px;
  font-weight: 500;
  line-height: 52px;
  margin-bottom: 62px;
  text-align: center;
  color: #333333;
}

.tab-form__tabs {
  display: flex;
  border-radius: 4px;
  border: 1px solid #e7eef2;
  filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0.1));
}

.tab-form__tab {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  outline: none;
  flex: 1 1 50%;
  height: 125px;
  font-size: 22px;
  line-height: 28px;
  color: #333333;
  border: 1px solid transparent;
  transition: all 0.3s ease;
}

.tab-form__tab:hover,
.tab-form__tab:focus {
  background-color: rgba(231, 238, 242, 0.5);
}

.tab-form__tab.active {
  background: #e7eef2;
  border-color: #e7eef2;
}

.tab-form__views {
  top: -1px;
  background: #ffffff;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  overflow: hidden;
}

.tab-form__view {
  position: relative;
  display: none;
}

.tab-form__view.active {
  display: flex;
}

.tab-form__view-wrapper {
  max-width: 612px;
  padding: 75px 90px 50px;
  box-sizing: content-box;
}

.tab-form__title {
  margin-bottom: 11px;
  font-size: 25px;
  line-height: 36px;
  font-weight: 500;
  display: flex;
  align-items: center;
  color: #333333;
}

.tab-form__description {
  max-width: 612px;
  margin-bottom: 19px;
  font-size: 16px;
  line-height: 26px;
  color: #333333;
  font-weight: 300;
}

.tab-form__description strong {
  color: #333333;
  font-weight: 500;
}

.tab-form__form {
  display: flex;
  flex-wrap: wrap;
}

.tab-form__form:focus-within input[required]:invalid,
.tab-form__form:focus-within textarea[required]:invalid {
  border-color: #ea4e25;
}

.tab-form__input {
  flex: 1 1 auto;
  height: 60px;
  border: 1px solid #dadada;
  outline: none;
  border-radius: 6px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-size: 16px;
  line-height: 20px;
  color: #787878;
  padding-left: 28px;
  padding-right: 28px;
  transition: all 0.3s ease;
}

.tab-form__input-wrapper {
  flex: 1 1 auto;
}

.tab-form__input-wrapper input {
  width: 100%;
}

.tab-form__input:focus {
  border-color: #277792;
}

.tab-form__input::placeholder {
  font-size: 16px;
  line-height: 20px;
  color: #787878;
}

.tab-form__input:first-child {
  margin-right: 16px;
}

.tab-form__textarea {
  flex: 1 1 100%;
  margin-top: 12px;
  border: 1px solid #dadada;
  border-radius: 6px;
  padding-top: 19px;
  padding-bottom: 19px;
  padding-left: 28px;
  padding-right: 28px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  min-height: 175px;
  outline: none;
  font-size: 16px;
  line-height: 20px;
  color: #787878;
  padding-left: 28px;
  padding-right: 28px;
  transition: all 0.3s ease;
}

.tab-form__textarea:focus {
  border-color: #277792;
}

.tab-form__textarea::placeholder {
  font-size: 16px;
  line-height: 20px;
  color: #787878;
}

.tab-form__footer {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 19px;
}

.tab-form__errors {
  margin-top: 19px;
}

.tab-form__error {
  display: block;
  font-size: 12px;
  line-height: 15px;
  color: #d03636;
}

.tab-form__error:not(:last-child) {
  margin-bottom: 5px;
}

.tab-form__footer-agree {
  display: inline-flex;
  align-items: center;
  font-size: 12px;
  line-height: 15px;
  color: #333333;
  margin-right: auto;
}

.tab-form__agree {
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  border: 0;
  background-color: transparent;
  outline: none;
}

.tab-form__agree.active .tab-form__agree-icon {
  opacity: 1;
}

.tab-form__agree-icon-wrapper {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #333;
  border-radius: 50%;
  margin-right: 6px;
  width: 18px;
  height: 18px;
  min-width: 18px;
  min-height: 18px;
  pointer-events: none;
}

.tab-form__agree-icon {
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s ease;
}

.tab-form__agree-rule {
  pointer-events: none;
}

.tab-form__agree-link {
  margin-left: 5px;
  display: inline-block;
  text-decoration: underline;
  text-decoration-color: #5890a9;
  color: #5890a9;
  white-space: nowrap;
}

.tab-form__agree-link:hover {
  text-decoration: none;
}

.tab-form__footer-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-left: 15px;
  padding-right: 15px;
  height: 60px;
  font-size: 15px;
  line-height: 19px;
  color: #333333;
  background: #ffffff;
  border: 1px solid #333333;
  box-sizing: border-box;
  border-radius: 30.5px;
  transition: all 0.3s ease;
}

.tab-form__footer-btn:hover,
.tab-form__footer-btn:focus {
  background-color: #333;
  color: #fff;
}

.tab-form__footer-btn[disabled="true"] {
  cursor: default;
}

.tab-form__footer-btn[disabled="true"]:hover {
  background-color: #ffffff;
  color: #333333;
}

.tab-form__footer-btn_1 {
  min-width: 265px;
}

.tab-form__footer-btn_2 {
  min-width: 248px;
}

.tab-form__bg-image {
  position: absolute;
  right: 0;
  top: 157px;
  height: 753px;
  transition: all 0.5s ease;
}

.tab-form__bg-image_2 {
  height: 600px;
  top: 62px;
}

@media (max-width: 991px) {
  .tab-form__tab {
    height: 90px;
  }

  .tab-form__footer-btn_1 {
    min-width: unset;
  }

  .tab-form__footer-btn_2 {
    min-width: unset;
  }

  .tab-form__bg-image {
    height: auto;
    width: 20%;
    bottom: unset;
    top: 50px;
  }
}

@media (max-width: 768px) {
  .tab-form__views {
    overflow: hidden;
  }

  .tab-form__view-wrapper {
    max-width: unset;
    padding: 40px 25px 315px 25px;
  }

  .tab-form__bg-image {
    height: auto;
    width: 217px;
    bottom: -135px;
    top: unset;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (max-width: 767px) {
  .tab-form__main-title {
    margin-bottom: 32px;
    font-size: 28px;
    line-height: 30px;
  }

  .tab-form__tab {
    height: 83px;
  }
}

@media (max-width: 576px) {
  .tab-form__title {
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 15px;
  }

  .tab-form__description strong {
    display: inline-block;
    max-width: 270px;
  }

  .tab-form__description {
    font-size: 15px;
    line-height: 26px;
    margin-bottom: 26px;
  }

  .tab-form__form {
    display: block;
  }

  .tab-form__input-wrapper {
    width: 100%;
    margin-bottom: 10px;
  }

  .tab-form__input {
    margin-bottom: 10px;
    font-size: 15px;
    line-height: 19px;
    width: 100%;
  }

  .tab-form__input::placeholder {
    font-size: 15px;
    line-height: 19px;
  }

  .tab-form__input:first-child {
    margin-right: 0;
  }

  .tab-form__textarea {
    margin-top: 0;
    width: 100%;
  }

  .tab-form__footer {
    margin-top: 13px;
    display: block;
  }

  .tab-form__errors {
    margin-top: 12px;
  }

  .tab-form__error {
    font-size: 11px;
    line-height: 16px;
  }

  .tab-form__footer-agree {
    margin-right: 0;
    margin-bottom: 10px;
    font-size: 11px;
  }

  .tab-form__footer-btn {
    width: 100%;
  }
}

@media (max-width: 575px) {
  .tab-form__tab {
    font-size: 15px;
    line-height: 19px;
  }
}